<template>
	<main class="anw">
		<section>
			<ul>
				<li @click="change(item.active)" v-for="item in labels" :class="{active:item.active==nowtable}">{{item.label}}</li>
			</ul>
			<div class="content">
				<div class="item" v-for="item in comments">
					<div class="avater">
						<div class="img">
							<img :src="item.avater" alt="">
							<span>{{item.username}}</span>
						</div>
						<div class="vote">
							<img v-for="item in parseInt(item.vote)" src="../../assets/images/syxq/star@2x.png"alt="">
							<img v-for="item in String(item.vote).indexOf('.')!=-1?1:0" src="../../assets/images/syxq/halfstar@2x.png" alt="">
							<img v-for="item in 5-Math.ceil(item.vote)" src="../../assets/images/syxq/nonestar@2x.png" alt="">
						</div>
					</div>
					<div class="cc">
						{{item.content}}
					</div>
					<div class="time">
						<span class="nurse">服务护士: {{item.nurse}}</span>
						<span class="created">{{item.created_at}}</span>
					</div>
				</div>
			</div>
			<div class="more">
				{{top}}
			</div>
		</section>
	</main>
</template>
<script>
export default{
	data(){
		return{
			top:'已无更多内容',
			nowtable:1,
			labels:[{'active':1,'label':'全部'},{'label':'上门速度快'},{'active':2,'label':'服务态度好'},{'active':3,'label':'服务非常专业'},{'active':4,'label':'手法棒'},{'active':5,'label':'服务态度棒'},{'active':6,'label':'上门速度快'}],
			//点击tab更换item来更改评论内容
			comments:[{'avater':require('../../assets/images/syxq/avater@2x.png'),'username':'Funny','vote':5,'content':'护士非常专业?','nurse':'张萌萌','created_at':'2017-12-01'},{'avater':require('../../assets/images/syxq/avater@2x.png'),'username':'Funnys','vote':4,'content':'护士非常专业','nurse':'张萌萌','created_at':'2017-11-07'},{'avater':require('../../assets/images/syxq/avater@2x.png'),'username':'Funnya','vote':1.5,'content':'护士非常专业a','nurse':'张萌萌','created_at':'2017-10-03'}],
		}
	},
	methods:{
		change(v){
			this.nowtable=v;
			//此处接口交换
		},
		appendanwser(){
			//在此处写加载下拉加载更多的方法
			this.top = '加载中..';
			this.comments.push({'avater':require('../../assets/images/syxq/avater@2x.png'),'username':'Funny','vote':5,'content':'护士非常专业?','nurse':'张萌萌','created_at':'2017-12-01'});
		}
	},
	mounted(){
		document.onscroll = ()=>{
			var top = document.documentElement.scrollTop||document.body.scrollTop||window.pageYOffset;
			if(document.querySelector('.content')){
				var eltop = document.querySelector('.content').offsetHeight;
				if(top>eltop){
					this.appendanwser();
				}
			}
			if(top>document.querySelector('.thelist').offsetTop&&document.querySelector('.thelist').offsetTop!=0){
			      	console.log(123);
		        var ul = document.querySelector('.thelist');
		        sessionStorage.off = document.querySelector('.thelist').offsetTop;
		        ul.style = 'position: fixed;top: 0;z-index: 99;width: 100%;background-color: #fff;'
		    }else if(document.querySelector('.thelist').offsetTop==0&&top<sessionStorage.off){
		        document.querySelector('.thelist').style = 'position: static;border:none;'     
		    }else{
				return false;
		    }
		}
	},
}
</script>
<style lang="scss" scoped>
	@import '../../assets/css/syxq/anw.scss'
</style>


