<template>
	<main class="fwxq">
		<header>
			<img src="../../assets/images/syxq/index@2x.png" alt="">
			<div class="titles">
				<div class="title">
					<h1>产后乳汁淤积<span class="discount">首单直降300元</span>
						<img src="../../assets/images/syxq/half@2x.png" alt="">
					</h1>
					<p><span class='cost'>￥</span>398<span class="time">/40~60分钟</span></p>
				</div>
				<div class="share">
					<svg viewBox="0 0 100 100" enable-background="new 0 0 100 100" xml:space="preserve">
						<image x="0" y="0" xlink:href="../../assets/images/syxq/xingzhuang@2x.png" />
					</svg>
					<p>分享</p>
				</div>
			</div>
			<ul class="choise">
				<li @click="hide=true"><p>选择 服务次数<span>(产后一周产妇建议选择连续三天服务)</span></p><img class="arrow" src="../../assets/images/syxq/Icon-arrow@2x.png"/></li>
				<li @click="change('dptc')"><p>自由搭配</p><img class="arrow" src="../../assets/images/syxq/Icon-arrow@2x.png" /></li>
				<li>
					<div class="img">
						<img src="../../assets/images/syxq/p1@2x.png" alt="">
						<p>产后通乳</p>
					</div>
					<img src="../../assets/images/syxq/Icon-+@2x.png" alt="">
					<div class="img">
						<img src="../../assets/images/syxq/p2@2x.png" alt="">
						<p>产后催乳</p>
					</div>
					<img src="../../assets/images/syxq/Icon-+@2x.png" alt="">
					<div class="img">
						<img src="../../assets/images/syxq/p3@2x.png" alt="">
						<p>产后急性...</p>
					</div>
					<img @click="change('dptc2')" src="../../assets/images/syxq/Icon-more@2x.png" alt="">
				</li>
			</ul>
		</header>
		<section>
			<ul class="thelist" v-istop>
				<li @click="change('/')" :class="{active:bannernow==1}"><p>服务详情</p><span></span></li>
				<li @click="change('quest')" :class="{active:bannernow==2}"><p>常见问题</p><span></span></li>
				<li @click="change('anwser')" :class="{active:bannernow==3}"><p>服务评论</p><span></span></li>
			</ul>
			<router-view></router-view>
			<!-- 遮罩层 -->
			<div v-if="hide" class="hidepanel"></div>
			<div v-if="hide" class="hidecontent">
				<div class="head">
					<div class="avater">
						<img src="../../assets/images/syxq/avater.png" alt="">
					</div>
					<div class="cost">
						<p><span>￥</span>666<span>原价：￥1194</span></p>
						<span>40~60分钟</span>
					</div>
					<img @click="hide=false" src="../../assets/images/syxq/Icon-close@2x.png" alt="" class="close">
				</div>
				<div class="choise">
					<h3>请选择服务次数</h3>
					<span>（建议选择连续3天服务套餐，尤其产后一周产妇）</span>
					<ul>
						<li @click="cishu=1" :class="{active:cishu==1}">单次</li>
						<li @click="cishu=2" :class="{active:cishu==2}">双次</li>
						<li @click="cishu=3" :class="{active:cishu==3}">三次</li>
					</ul>
				</div>
				<div class="info">
					注：选择多次服务的用户填写的订单信息是用于第一次的服务时间地址，如需进行第二、三次服务，请联系客服更改或在 我的-我的订单 中更改信息
				</div>
			</div>
		</section>
		<footer>
			立即预约
		</footer>
	</main>
</template>
<script>
import 'lib-flexible'
export default{
	data(){
		return{
			hide:false,
			bannernow:1,
			cishu:1,
		}
	},
	methods:{
		change(v){
			this.bannernow = v=='/'?1:v=='quest'?2:3;
			if(v=='/'){
				this.$router.replace({path:'/fwxq'});
			}else if(v=='quest'||v=='anwser'){
				this.$router.replace({path:'/fwxq/'+v});
			}else{
				this.$router.push({name:v});
			}
		}
	},
	beforeRouteLeave (to, from, next) {
    	document.onscroll = () => {
    		return false;
    	}
    	next();
  	},
	mounted(){
		if(this.$route.path.indexOf('quest')!=-1){
			this.bannernow = 2;
		}else if(this.$route.path.indexOf('anwser')!=-1){
			this.bannernow = 3;
		}else{
			this.bannernow = 1;
		}
	},	
	directives:{
		istop:{
			bind:(el)=>{
				document.onscroll = ()=>{
				  var off;
				  var top = document.documentElement.scrollTop||document.body.scrollTop||window.pageYOffset;
			      if(top>el.offsetTop&&el.offsetTop!=0){
			      	console.log(123);
			        var ul = document.querySelector('.thelist');
			        sessionStorage.off = el.offsetTop;
			        ul.style = 'position: fixed;top: 0;z-index: 99;width: 100%;background-color: #fff;'
			      }else if(el.offsetTop==0&&top<sessionStorage.off){
			        document.querySelector('.thelist').style = 'position: static;border:none;'     
			      }else{
					return false;
			      }
			    }
			}
		}
	},
}
</script>
<style lang="scss" scoped>
	@import '../../assets/css/syxq/syxq.scss'
</style>


