<template>
    <div class="swiper-container" id="banner">
        <div class="swiper-wrapper">
            <div class="swiper-slide" v-for="(str,index) in listImg" :key="index" :style="{ backgroundImage: 'url(' + str.url + ')' }"></div>
        </div>
        <div class="swiper-pagination my-pagination"></div>
    </div>
</template>

<script>
import Swiper from '../../../static/swiper/dist/js/swiper.min.js';
import '../../../static/swiper/dist/css/swiper.min.css';

export default {
  props: ["listImg"],
  mounted() {
    var swiper = new Swiper("#banner", {
      pagination: {
        el: ".swiper-pagination"
      },
      loop: true,
      speed: 600,
      autoplay: 4000
    });
  }
};
</script>

<style lang="scss" scoped>

.swiper-container {
  width: 100%;
  height: 100%;
  .swiper-wrapper {
    width: 100%;
    height: 100%;
  }
  .swiper-slide {
    background-position: center;
    background-size: cover;
    width: 100%;
    height: 100%;
  }
}
</style>


