<template>
  <div class="page" :style="'height:'+winHEIGHT+'px'">
    <div class="flex-top">
        <div class="top-content">
            <div class="content-top">
                <div class="top-item">
                    <div class="nurses-photo">
                        <img src="./images/nurses_default.jpg" alt="">
                    </div>
                    <div class="right-item">
                        <span class="icon icon-005"></span>
                        <span class="icon icon-006"></span>
                    </div>
                </div>
                <div class="top-item2">
                    <div class="item-inner">
                        <span class="item-inner-num">{{order.createrTime}}</span>
                        <span class="item-inner-title">最近订单开始时间</span>
                    </div>
                    <div class="item-inner">
                        <span class="item-inner-num">{{order.count}}</span>
                        <span class="item-inner-title">今日订单数</span>
                    </div>
                </div>
            </div>
            <div class="content-canvas" :style="'width:'+winWIDTH+'px;overflow:hidden;'">
                <canvas ref="dashCanvas" :class="{ 'dash': order.now>=0, 'dash2': order.now>15, 'dash3': order.now>50}"></canvas>
                <div class="dash-info">
                    <span class="dash-title" v-if="order.hasOrder">{{order.title}}</span>
                    <span :class="order.now>55?'dash-time dash-warning':'dash-time'" v-if="order.hasOrder">{{order.newText}}</span>
                    <span class="dash-unit" v-if="order.hasOrder">min</span>
                    <span class="dash-no-order" v-if="!order.hasOrder">{{order.text}}</span>
                </div>
            </div>
            <div class="content-bottom">
                <p class="address">{{order.address}}</p>
                <span class="btn btn-face-1" v-if="order.hasOrder&&order.total<=15">确认订单</span>
                <span class="btn btn-face-1" v-if="order.hasOrder&&order.total>15&&order.total<=50">确认出发</span>
                <span class="btn btn-face-2" v-if="order.hasOrder&&order.total>50">服务中</span>
            </div>
        </div>
        <canvas ref="waveCanvas" class="wave-box"></canvas>
    </div>
    <div class="flex-bottom">
        <div class="flex-bottom-row bbline">
            <div class="flex-bottom-item brline">
                <span class="item-text">1</span>
                <span class="item-icon-text icon-001">待确定订单</span>
            </div>
            <div class="flex-bottom-item">
                <span class="item-text">0</span>
                <span class="item-icon-text icon-002">待出诊订单</span>
            </div>
        </div>
        <div class="flex-bottom-row">
            <div class="flex-bottom-item brline">
                <span class="item-text">1</span>
                <span class="item-icon-text icon-003">服务中订单</span>
            </div>
            <div class="flex-bottom-item">
                <span class="item-text">0</span>
                <span class="item-icon-text icon-004">全部订单</span>
            </div>
        </div>
    </div>
  </div>
</template>

<<script>
export default {
    name:"NursesIndex",
    data:function(){
        return{
            winHEIGHT:0,
            winWIDTH:0,
            start: 0,
            total: 15,
            now: 5,//订单状态0
            order0:{
                hasOrder:false,
                createrTime:"00:00",
                count:0,
                text:"暂无订单",
                start: 0,
                total: 0,
                now: 0,
                newText:"",
                address:""
            },//订单状态1
            order1:{
                hasOrder:true,
                createrTime:"09-09:12:00",
                title:"产后催乳",
                count:1,
                start: 0,
                total: 15,
                now: 5,
                newText:"05:00",
                address:"南京市鼓楼区龙宝西地园25栋601"
            },//订单状态2
            order2:{
                hasOrder:true,
                createrTime:"09-09:12:00",
                title:"产后催乳",
                count:1,
                start: 0,
                total: 50,
                now: 20,
                newText:"26:00",
                address:"南京市鼓楼区龙宝西地园25栋601"
            },//订单状态3
            order3:{
                hasOrder:true,
                createrTime:"09-09:12:00",
                title:"产后催乳",
                count:1,
                start: 0,
                total: 60,
                now: 50,
                newText:"60:03",
                address:"南京市鼓楼区龙宝西地园25栋601"
            },
            order:{}
        }
    },
    beforeRouteEnter: (to, from, next) => {
        next((vm)=>{
            vm.winHEIGHT =  window.screen.height;
            vm.winWIDTH =  window.screen.width;
            vm.$nextTick(function () {
                vm.loadWave();
            })
        })
    },
    mounted:function(){
        this.loadDash();
        this.order = this.order2;
    },
    methods:{
        loadDash:function(){
            let dashCanvas = this.$refs.dashCanvas;
            function draw(context, lineWidth, color, radius, start, end,shadow) {
                context.save();
                context.beginPath();
                context.lineCap = "round";
                context.lineWidth = lineWidth;
                context.strokeStyle = color;
                context.fillStyle = color;
                if(shadow){
                    context.shadowOffsetX = 0;
                    context.shadowOffsetY = 0;
                    context.shadowColor = '#fff';
                    context.shadowBlur = 5;
                }
                context.arc(dashCanvas.width/2-2, dashCanvas.height/2+35, radius, start, end, false);
                context.stroke();
                context.closePath();
                context.restore();
            }
            var interval = null;
            if(dashCanvas.getContext){  
                /*
                size：圆环大小
                radius：圆环半径
                lineWidth：圆环线宽
                startDeg：开始角度
                endDeg：灰色圆环结束角度
                */ 
                var context = dashCanvas.getContext("2d");  
                let size = 100;
                let radius = dashCanvas.height/2+30;
                let endDeg = 30;
                const lineWidth = 8;
                const rad = Math.PI / 180;
                const startDeg = 60 - 360 * 270 / 360;
                dashCanvas.width = 375;
                dashCanvas.height = 375;
                context.clearRect(0, 0, this.winWIDTH, this.winWIDTH);
                draw(context,lineWidth,'#978af7',radius,startDeg * rad,endDeg * rad,true);
                let average = startDeg+240*this.order.now/this.order.total;
                draw(context,lineWidth,'#fff',radius,startDeg * rad,average * rad,false);
                interval = setInterval(() => {
                	if(this.order.now<this.order.total){
                		this.order.now = this.order.now+1
                		average = startDeg+240*this.order.now/this.order.total;
                		draw(context,lineWidth,'#fff',radius,startDeg * rad,average * rad,false);
                	}else{
                        clearInterval(interval)
                    }
                }, 1000)
            
            }
        },
        loadWave:function(){
            let graph = this.$refs.waveCanvas;
            let ctx = graph.getContext('2d');
            graph.width =  this.winWIDTH;
            graph.height = 60;
            window.requestAnimFrame = (function(){ 
                return window.requestAnimationFrame  || 
                    window.webkitRequestAnimationFrame || 
                    window.mozRequestAnimationFrame || 
                    function( callback ){ 
                    window.setTimeout(callback, 1000 / 60); 
                }; 
            })(); 
            // 波浪大小
            var boHeight = graph.height / 2;
            var posHeight = graph.height / 1.2;
            //初始角度为0 
            var step = 0; 
            //定义三条不同波浪的颜色 
            var lines = ["rgba(170,158,248, 0.7)", "rgba(170,158,248, 0.8)", "rgba(170,158,248, 0.5)"]; 
            function loop(){ 
                ctx.clearRect(0,0,graph.width,graph.height); 
                step++; 
                //画3个不同颜色的矩形 
                for(var j = lines.length - 1; j >= 0; j--) { 
                    ctx.fillStyle = lines[j]; 
                    //每个矩形的角度都不同，每个之间相差45度 
                    var angle = (step+j*50)*Math.PI/180; 
                    var deltaHeight = Math.sin(angle*4/3) * boHeight;
                    var deltaHeightRight = Math.cos(angle*4/3) * boHeight; 
                    ctx.beginPath();
                    ctx.moveTo(0, posHeight+deltaHeight); 
                    ctx.bezierCurveTo(graph.width/2, posHeight+deltaHeight-boHeight, graph.width / 2, posHeight+deltaHeightRight-boHeight, graph.width, posHeight+deltaHeightRight); 
                    ctx.lineTo(graph.width, graph.height); 
                    ctx.lineTo(0, graph.height); 
                    ctx.lineTo(0, posHeight+deltaHeight); 
                    ctx.closePath(); 
                    ctx.fill(); 
                }
                requestAnimFrame(loop);
            } 
            loop(); 
        }
    }
}
</script>


<style lang="scss" scoped>
.page{
    display: flex;
    flex-direction: column;
    .flex-top{
        display: flex;
        flex:7;
        flex-direction: column;
        .top-content{
            flex:1;
            background: rgba(170,158,248, 1);
            display: flex;
            flex-direction: column;
            .content-top{
                flex: 1;
                display: flex;
                flex-direction: column;
                .top-item{
                    flex: 1;
                    display: flex;
                    padding: 0.2rem 0.3rem;
                    align-items: center;
                    .nurses-photo{
                        width: 1.2rem;
                        height: 1.2rem;
                        border-radius: 100%;
                        overflow: hidden;
                        img{
                            width:100%;
                            height: 100%;
                        }
                    }
                     @media screen and (max-width:360px){
                        .nurses-photo{
                            width: 0.8rem;
                            height: 0.8rem;
                        }
                     }
                    .right-item{
                        flex: 1;
                        text-align: right;
                        .icon{
                            display: inline-block;
                            width: 1rem;
                            height: 0.7rem;
                        }
                        .icon-005{
                            background: url(./images/icon_005.png) no-repeat center center;
                            background-size: contain;
                        }
                        .icon-006{
                            background: url(./images/icon_006.png) no-repeat center right;
                            background-size: contain;
                        }
                    }
                }
                .top-item2{
                    flex: 2;
                    display: flex;
                    align-items: center;
                    color: #fff;
                    text-align: center;
                    justify-content:space-between;
                    padding: 0 0.8rem;
                    .item-inner{
                        span{
                            display: block;
                        }
                        .item-inner-num{
                            font-size: 0.5rem;
                        }
                        .item-inner-title{
                            padding-top: 0.2rem;
                            font-size: 0.3rem;
                        }
                    }
                    
                }
            }
            .content-canvas{
                position: relative;
                height: 6rem;
                .dash{
                    position: absolute;
                    top: -2rem;
                    background: url(./images/dash_01.png) no-repeat center center;
                    background-size: 90%;
                }
                .dash2{
                    background: url(./images/dash_02.png) no-repeat center center;
                    background-size: 90%;
                }
                .dash3{
                    background: url(./images/dash_03.png) no-repeat center center;
                    background-size: 90%;
                }
                .dash-info{
                    position: absolute;
                    width: 3rem;
                    height: auto;
                    color: #fff;
                    text-align: center;
                    margin-left:50%;
                    left:-1.5rem;
                    top:2.5rem;
                    span{
                        display: block;
                    }
                    .dash-title{
                        font-size: 0.5rem;
                        margin:0.2rem 0;
                    }
                    .dash-time{
                        font-size: 1rem;
                    }
                    .dash-warning{
                        color: #fdcc01;
                    }
                    .dash-unit{
                        font-size: 0.4rem;
                    }
                    .dash-no-order{
                        font-size: 0.5rem;
                        margin-top:1.6rem;
                    }
                }
            }
            @media screen and (max-width:414px){
                .content-canvas{
                     height: 6rem;
                    .dash{
                        left: 0.5rem;
                        top: -1.5rem;
                    }
                }
            }
            @media screen and (max-width:375px){
                .content-canvas{
                    height: 6rem;
                    .dash{
                        top: -2rem;
                        left: 0;
                    }
                }
            }
            @media screen and (max-width:360px){
                .content-canvas{
                    height: 6.3rem;
                    .dash{
                        top: -2.1rem;
                        left: 0;
                    }
                }
            }
            @media screen and (max-width:320px){
                .content-canvas{
                     height: 7rem;
                    .dash{
                        top:-2.4rem;
                        left: -0.8rem;
                        background-size: 88%;
                    }
                    .dash-info{
                        .dash-time{
                            margin:0.5rem 0;
                        }
                    }
                }
            }
            .content-bottom{
                height: 1.5rem;
                padding-bottom: 0.5rem;
                text-align: center;
                .address{
                    color: #fff;
                    font-size: 0.4rem;
                }
                .btn{
                    display: inline-block;
                    margin-top: 0.4rem;
                    width:3.5rem;
                    font-size: 0.4rem;
                    font-weight: bold;
                    height: 1rem;
                    line-height: 1rem;
                    border-radius: 0.5rem;
                    letter-spacing: 1px;
                }
                .btn-face-1{
                    color: RGB(137,119,246);
                    background: #fff;
                    box-shadow:0px 0px  15px 5px #dadada;
                }
                .btn-face-2{
                    color: #fff;
                    background: rgba(175,164,249, 0.5);
                    box-shadow:0px 0px  15px 2px #beb4fa;
                    border:1px solid #ffffff;

                }
            }
        }
        .wave-box{
            width: 100%;
            transform: rotate(180deg);
        }
    }
    .flex-bottom{
        flex:2;
        display: flex;
        flex-direction: column;
        padding: 0 0.3rem 0.3rem 0.3rem;
        .bbline{
            border-bottom: 1px solid #e5e5e5;
        }
        .flex-bottom-row{
            display: flex;
            flex:1;
            flex-direction: row;
            .brline{
                border-right: 1px solid #e5e5e5;
            }
            .flex-bottom-item{
                flex:1;
                display: flex;
                justify-content: center;
                align-items: center;
                flex-direction: column;
                .item-text{
                    color: #8977f6;
                    font-size: 0.6rem;
                }
                .item-icon-text{
                    display: inline-block;
                    height: 0.8rem;
                    line-height: 0.8rem;
                    padding-left: 0.8rem;
                    color: #666666;
                    font-size: 0.4rem;
                }
                .icon-001{
                    background: url(./images/icon_001.png) no-repeat center left;
                    background-size: auto 0.6rem;
                }
                .icon-002{
                    background: url(./images/icon_002.png) no-repeat center left;
                    background-size: 0.6rem auto;
                }
                .icon-003{
                    background: url(./images/icon_003.png) no-repeat center left;
                    background-size: 0.6rem auto;
                }
                .icon-004{
                    background: url(./images/icon_004.png) no-repeat center left;
                    background-size: auto 0.6rem;
                }
            }
        }
    }
}
</style>




