<template>
    <div class="page">
        <div class="tab">
            <div v-for="(item,index) in tabList" :key="index" :class="tabIndex==index?'tab-item current':'tab-item'" @click="chengeTab(index)">
                <span>{{item}}</span>
                <i></i>
            </div>
        </div>
        <div class="tab-swiper">
            <div class="left-bar"></div>
            <div class="swiper-container" id="tab_swiper">
                <div class="swiper-wrapper">
                    <div class="swiper-slide">
                      <div class="slide-item mt30">
                        <span class="default">产后催乳</span>
                        <span class="default">产后通乳</span>
                      </div>
                      <div class="slide-item">
                        <span></span>
                        <span></span>
                      </div>
                      <div class="link-more"><span>查看更多</span></div>
                    </div>
                    <div class="swiper-slide">
                      <div class="slide-item mt30">
                        <span class="default">产后催乳</span>
                        <span class="default">产后通乳</span>
                      </div>
                      <div class="slide-item">
                        <span class="default">产后乳汁淤积</span>
                        <span class="active">产后急性乳腺炎</span>
                      </div>
                      <div class="link-more"><router-link to="/readmore"><span class="lick-face">查看更多</span></router-link></div>
                    </div>
                    <div class="swiper-slide">
                      <div class="slide-item mt30">
                        <span></span>
                        <span></span>
                      </div>
                      <div class="slide-item">
                        <span></span>
                        <span></span>
                      </div>
                      <div class="link-more"><span>查看更多</span></div>
                    </div>
                </div>
            </div>
            <div class="right-bar"></div>   
        </div>
        <div class="loop-swiper">
            <Banner :listImg="listImg"/>
        </div>
        <div class="main-list">
          <div class="main-title"><i></i><span>护士团队</span></div>
          <div class="content">
            <ul>
              <li class="item">
                <div class="item-wraper">
                  <div class="item-pic">
                    <img src="./images/nurses_pic.png" alt="">
                  </div>
                  <div class="item-info">
                    <div><span class="info-name">张萌</span><span class="tag tag-primary">护士长</span><span class="tag">工作8年</span></div>
                    <div><p class="info-desc">擅长：催乳、通乳、治疗急性乳腺炎</p></div>
                  </div>
                </div>
              </li>
              <li class="item">
                <div class="item-wraper">
                  <div class="item-pic">
                    <img src="./images/nurses_pic.png" alt="">
                  </div>
                  <div class="item-info">
                    <div><span class="info-name">张萌</span><span class="tag tag-primary">护士长</span><span class="tag">工作8年</span></div>
                    <div><p class="info-desc">擅长：催乳、通乳、治疗急性乳腺炎</p></div>
                  </div>
                </div>
              </li>
              <li class="item">
                <div class="item-wraper">
                  <div class="item-pic">
                    <img src="./images/nurses_pic.png" alt="">
                  </div>
                  <div class="item-info">
                    <div><span class="info-name">张萌</span><span class="tag tag-primary">护士长</span><span class="tag">工作8年</span></div>
                    <div><p class="info-desc">擅长：催乳、通乳、治疗急性乳腺炎</p></div>
                  </div>
                </div>
              </li>
              <li class="item">
                <div class="item-wraper">
                  <div class="item-pic">
                    <img src="./images/nurses_pic.png" alt="">
                  </div>
                  <div class="item-info">
                    <div><span class="info-name">张萌</span><span class="tag tag-primary">护士长</span><span class="tag">工作8年</span></div>
                    <div><p class="info-desc">擅长：催乳、通乳、治疗急性乳腺炎</p></div>
                  </div>
                </div>
              </li>
              <li class="item">
                <div class="item-wraper">
                  <div class="item-pic">
                    <img src="./images/nurses_pic.png" alt="">
                  </div>
                  <div class="item-info">
                    <div><span class="info-name">张萌</span><span class="tag tag-primary">护士长</span><span class="tag">工作8年</span></div>
                    <div><p class="info-desc">擅长：催乳、通乳、治疗急性乳腺炎</p></div>
                  </div>
                </div>
              </li>
            </ul>
          </div>
        </div>
        <Navbar/>
    </div>
</template>

<script>
import Swiper from '../../../static/swiper/dist/js/swiper.min.js';
import Navbar from '@/components/navbar.vue'
import Banner from './Banner.vue' //导入Banner组件
var tabSwiper = null;
export default {
  name: "NursesIndex",
  data: function() {
    return {
      tabList: ["骨科", "产后康复", "消化科"],
      listImg: [
        {url: require("./images/banner.jpg")}, 
        {url: require("./images/banner.jpg")}, 
        {url: require("./images/banner.jpg")}
      ],
      tabIndex: 1
    };
  },
  components: {
    'Navbar':Navbar,
    'Banner': Banner,
  },
  mounted: function() {
    var self = this;
    tabSwiper = new Swiper("#tab_swiper", {
      slidesPerView: 1.2,
      centeredSlides: true,
      spaceBetween: 20
    });
    tabSwiper.on("slideChangeTransitionEnd",function(){
      self.tabIndex = this.activeIndex;
    })
    tabSwiper.slideTo(self.tabIndex, 0, false);
  },
  methods: {
    chengeTab: function(index) {
      this.tabIndex = index;
      tabSwiper.slideTo(index, 500, false);
    }
  }
};
</script>


<style lang="scss" scoped>
@import '../../../static/swiper/dist/css/swiper.min.css';
.page {
  height: auto;
  overflow: auto;
  padding-bottom: 100px;
  .tab {
    display: flex;
    height: 90px;
    background: #fff;
    border-bottom: 1px solid #e5e5e5;
    justify-content: center;
    align-items: center;
    .tab-item {
      display: flex;
      color: #333333;
      height: 90px;
      justify-content: center;
      align-items: center;
      flex-direction: column;
      padding: 0 20px;
      span {
        flex: 1;
        line-height: 85px;
        font-size: 28px;
        letter-spacing: 2px;
      }
      i {
        height: 5px;
        width: 40px;
        background: #fff;
        border-radius: 3px;
      }
    }
    .current {
      span {
        color: #8977f6;
        font-weight: bold;
      }
      i {
        background: #8977f6;
      }
    }
  }
  .tab-swiper {
    position: relative;
    height: 360px;
    padding: 20px 0;
    overflow: hidden;
    background: #f0f0f0;
    .left-bar,
    .right-bar {
      position: absolute;
      width: 50px;
      height: 285px;
      border-radius: 20px;
      background: #fff;
      top: 57px;
    }
    .left-bar {
      left: -20px;
    }
    .right-bar {
      right: -20px;
    }
    .swiper-container {
      width: 100%;
      height: 360px;
      .swiper-slide {
        font-size: 18px;
        background: #fff;
        display: flex;
        flex-direction: column;
        border-radius: 20px;
        .mt30{
          margin-top:30px;
        }
        .slide-item{
          flex:1;
          padding: 30px 35px 0;
          display: flex;
          span{
            flex:1;
            margin:0 15px;
            background: #f6f6f6;
            border-radius:20px;
            display: flex;
            align-items: center;
            justify-content: center;
            font-size: 28px;
            font-weight: bold;
            font-family:"Microsoft YaHei";
          }
          span.active{
            background: #5b3bd2;
            color: #fff;
          }
          span.default{
            background: #8977f6;
            color: #fff;
          }
        }
        .link-more{
          height: 90px;
          line-height: 90px;
          text-align: center;
          span{
            font-size: 24px;
            color: #f0f0f0;
            padding-right: 35px;
            background: url(./images/icon_arr_r.png) no-repeat center right;
            background-size: 35px; 
          }
          span.lick-face{
            background: url(./images/icon_arr_r2.png) no-repeat center right;
            background-size: 35px; 
            color: #8977f6;
          }
        }
      }
      .swiper-slide-prev,
      .swiper-slide-next {
        height: 285px;
        margin-top: 37px;
      }
    }
  }
  .loop-swiper {
    height: 180px;
    background: #f0f0f0;
    padding-bottom: 20px;
  }
  .main-list{
    padding: 0 30px;
    .main-title{
      height: 38px;
      padding: 25px 0;
      border-bottom: 1px solid #e5e5e5;
      display: flex;
      align-items: center;
      i{
        height: 30px;
        width: 10px;
        background: #8977f6;
        border-radius: 3px;
      }
      span{
        font-size: 34px;
        color: #666;
        padding-left: 20px;
      }
    }
    .content{
      flex: 1;
      padding-top: 30px;
      .item{
        height: 140px;
        background: #f6f6f6;
        border-radius: 10px;
        padding: 20px;
        margin-bottom:20px;
        overflow: hidden;
        display: flex;
        .item-wraper{
          flex: 1;
          display: flex;
          .item-pic{
            width: 140px;
            height: 140px;
            border-radius: 10px;
            padding-right: 30px;
            overflow: hidden;
            img{
              width: 100%;
              height: 100%;
            }
          }
          .item-info{
            flex: 1;
            display: flex;
            flex-direction: column;
            justify-content: center;
            >div{
              width: 475px;
              padding: 10px 0;
              span{
                display: inline-block;
              }
              span.info-name{
                color: #333333;
                font-size: 28px;
                font-weight: bold;
                margin-right: 25px;
              }
              span.tag{
                padding:4px 20px;
                font-size: 24px;
                color: #999999;
                border:1px solid #cccccc;
                border-radius:5px;
                margin-right: 10px;
              }
              span.tag-primary{
                color: #8977f6;
                background: #ebe9f6;
                border-color: #b9b0f6;
              }
              p.info-desc{
                display: block;
                width: 100%;
                font-size: 28px;
                color: #999999;
                overflow: hidden;
                white-space: nowrap;
                text-overflow: ellipsis;
              }
            }
          }
        }
      }
    }
  }
}
</style>




