import Vue from 'vue'
import Router from 'vue-router'
import myOrderForm from '@/pages/myOrderForm'
import question from '@/pages/question'
import toBeSetOut from '@/pages/toBeSetOut'
import toArrive from '@/pages/toArrive'
import undetermined from '@/pages/undetermined'
import WaitForTheDeparture from '@/pages/WaitForTheDeparture'
import server from '@/pages/server'
import cancel from '@/pages/cancel'
import NursesIndex from '@/pages/nurses/Index'

import patientMessage from '@/pages/message/index'
import Index from '@/pages/main/Index'
import ReadMore from '@/pages/main/ReadMore'

import fwxq from '@/pages/syxq/fwxq'
import dptc from '@/pages/syxq/dptc'
import dptc2 from '@/pages/syxq/dptc2'
import service from '../components/syxq/service'
import quest from '../components/syxq/quest'
import anwser from '../components/syxq/anwser'

Vue.use(Router)

export default new Router({
  routes: [{
    path: '/myOrderForm',
    name: 'myOrderForm',
    component: myOrderForm //我的订单
  }, {
    path: '/toBeSetOut',
    name: 'toBeSetOut',
    component: toBeSetOut //待出发
  }, {
    path: '/toArrive',
    name: 'toArrive',
    component: toArrive //待出发
  }, {
    path: '/undetermined',
    name: 'undetermined',
    component: undetermined //待确定
  }, {
    path: '/WaitForTheDeparture',
    name: 'WaitForTheDeparture',
    component: WaitForTheDeparture //等待出发
  }, {
    path: '/server',
    name: 'server',
    component: server //服务中
  }, {
    path: '/cancel',
    name: 'cancel',
    component: cancel //取消订单
  }, {
    path: '/nursesindex', //护士端  首页
    name: 'nursesindex',
    component: NursesIndex
  },{
    path: '/patientMessage', //患者端 消息
    name: 'patientMessage',
    component: patientMessage
  }, {
    path: '/index', //主页
    name: 'index',
    component: Index
  }, {
    path: '/readmore', //查看更多
    name: 'ReadMore',
    component: ReadMore
  }, {
    path: '/fwxq', //首页详情服务详情
    name: 'fwxq',
    component: fwxq,
    children: [{
      path: '/',
      component: service,
    }, {
      path: 'quest',
      component: quest,
    }, {
      path: 'anwser',
      component: anwser,
    }]
  }, {
    path: '/dptc', //搭配套餐
    name: 'dptc',
    component: dptc
  }, {
    path: '/dptc2', //搭配套餐2
    name: 'dptc2',
    component: dptc2
  },{
    path: '/question', //全部问题
    name: 'question',
    component: question
  }]
})



