<template>
	<main class="dptc">
		<section class="dptc2">
			<div class="intr">
				<h3>选择 搭配服务</h3>
				<span>（推荐搭配：产后乳汁淤积+产后通乳）</span>
			</div>
			<div class="content">
				<div class="item" v-for="(item,index) in produces">
					<div>
						<div @click="item.choise=='default'?false:choise(item.choise,index)">
							<img v-if="item.choise!='default'&&item.choise!='active'" src="../../assets/images/syxq/Icon-none@2x.png" alt="">
							<img v-if="item.choise=='default'" src="../../assets/images/syxq/Icon-check50@2x.png" alt="">
							<img v-if="item.choise=='active'" src="../../assets/images/syxq/Icon-check@2x.png" alt="">
						</div>
						<div><img :src="item.avater" alt=""></div>
						<div>
							<h3>{{item.name}}</h3>
							<h6>{{item.time}}</h6>
							<p><span>￥<span>{{item.nprice}}</span></span><span>原价:<span>￥{{item.bprice}}</span></span></p>
						</div>
					</div>
					<div>
						<span>x{{item.number}}</span>
						<img src="../../assets/images/syxq/Icon-arrow@2x.png" alt="">
					</div>
				</div>			
			</div>
		</section>
		<footer>
			<div class="price">
				<p>搭配价<span>  ￥<span>420</span></span></p>
				<span>优惠: ￥88</span>
			</div>
			<button>立即预约</button>
		</footer>
	</main>
</template>
<script>
export default{
	data(){
		return{
			produces:[{'choise':'default','avater':require('../../assets/images/syxq/avater.png'),'name':'掺和乳汁淤积','time':'40~60分钟','nprice':'210','bprice':'398','number':'1'},{'choise':'active','avater':require('../../assets/images/syxq/avater.png'),'name':'掺和乳汁淤积','time':'40~60分钟','nprice':'210','bprice':'398','number':'1'},{'choise':'','avater':require('../../assets/images/syxq/avater.png'),'name':'掺和乳汁淤积','time':'40~60分钟','nprice':'210','bprice':'398','number':'1'}]
		}
	},
	methods:{
		choise(v,i){
			if(v=='active'){
				this.produces[i].choise = '';
			}else{
				this.produces[i].choise = 'active';
			}
		}
	}
}
</script>
<style lang="scss" scoped>
	@import '../../assets/css/syxq/dptc.scss';
</style>


