<template>
    <div class="page" :style="'height:'+winHEIGHT+'px'">
        <div class="tip-header">
            <div><span>请选择服务内容</span></div>
        </div>
        <div class="more-list">
            <ul>
                <li class="item" v-for="(item,index) in list" :key="index" :class="index==activeindex?'item active':'item'" @click="doClick(index)">
                    <a class="item-inner"><span>{{item}}</span></a>
                </li>
                <!-- <li class="item">
                    <a class="item-inner"><span>产后通乳</span></a>
                </li>
                <li class="item">
                    <a class="item-inner"><span>产后乳汁淤积</span></a>
                </li>
                <li class="item">
                    <a class="item-inner"><span>产后急性乳腺炎</span></a>
                </li>
                <li class="item">
                    <a class="item-inner"><span>新生儿沐浴</span></a>
                </li>
                <li class="item">
                    <a class="item-inner"><span>新生儿按摩</span></a>
                </li> -->
            </ul>
        </div>
    </div>
</template>

<script>
export default {
    name:"ReadMore",
    data:function(){
        return{
            winHEIGHT:0,
            winWIDTH:0,
            activeindex:"",
            list:["产后催乳","产后通乳","产后乳汁淤积","产后急性乳腺炎","新生儿沐浴","新生儿按摩"]
        }
    },
    beforeRouteEnter: (to, from, next) => {
        next((vm)=>{
            vm.winHEIGHT =  window.screen.height;
            vm.winWIDTH =  window.screen.width;
        })
    },
    methods:{
        doClick:function(index){
            this.activeindex = index;
        }
    }
}
</script>


<style lang="scss" scoped>
.page{
    .tip-header{
        height: 100px;
        background: #f0f0f0;
        padding-bottom: 20px;
        display: flex;
        div{
            flex: 1;
            background: #fff;
            line-height: 100px;
            padding-left: 30px;
            span{
                font-size: 30px;
                color: #999999;
            }
        }
    }
    .more-list{
        .item{
            height: 100px;
            padding-left: 30px;
            display: flex;
            align-items: center;
            justify-content: center;
            .item-inner{
                flex: 1;
                height: 100px;
                border-bottom:1px solid #e5e5e5;
                display: flex;
                flex-direction: column;
                padding-right: 30px;
                span{
                    color: #666666;
                    height: 100px;
                    line-height: 100px;
                    font-size: 34px;
                    padding-right: 30px;
                    background: url(./images/icon_arrow.png) no-repeat center right;
                    background-size: 24px;
                }
            }
        }
        .active{
            background: #f6f6f6;
        }
    }
}
</style>




