<template>
  <div class="myOrderForm">
    <mOFheader title="我的订单"></mOFheader>
    <nav ref="nav">
      <span :class="nav === '全部'?'active':''" @click="change($event)">全部</span>
      <span :class="nav === '待确定'?'active':''" @click="change($event)">待确定</span>
      <span :class="nav === '待出诊'?'active':''" @click="change($event)">待出诊</span>
      <span :class="nav === '待服务'?'active':''" @click="change($event)">待服务</span>
    </nav>
    <section id="index-scroll" ref="wrapper">
      <ul>
        <div class="refresh">
          <img src="../assets/images/myOrderForm/loading.png" alt=""><span>{{top}}...</span>
        </div>
        <li v-for="item in list" v-if="item.status === nav || nav === '全部'">
          <h2 v-if="item.status === '待出诊'">
            <span class="time time0">{{item.time}}</span>
            <span class="status0">{{item.status}}</span>
          </h2>
          <h2 v-if="item.status === '待确定'">
            <span class="time time1">{{item.time}}</span>
            <span class="status1">{{item.status}}</span>
          </h2>
          <h2 v-if="item.status === '服务中'">
            <span class="time time0">{{item.time}}</span>
            <span class="status0">{{item.status}}</span>
          </h2>
          <h2 v-if="item.status === '服务完成'">
            <span class="time time2">{{item.time}}</span>
            <span class="status2">{{item.status}}</span>
          </h2>
          <section class="detail">
            <img src="../assets/images/myOrderForm/ru.png" alt="">
            <div>
              <p>患者： <span>{{item.userName}}</span></p>
              <p>服务项目：<span>{{item.fuwu}}</span></p>
            </div>
          </section>
          <section class="btn">
            <p></p>
            <div v-if="item.status === '待确定'" class="btn1">确认订单</div>
            <div v-if="item.status === '待出诊'" class="btn0">确认出发</div>
          </section>
        </li>
      </ul>
    </section>
  </div>
</template>

<script>
  import mOFheader from '../components/header.vue'
  import BScroll from 'better-scroll'
  import Vue from 'vue'
  export default {
    name: 'myOrderForm',
    data() {
      return {
        list: [{
          time: '服务时间：2017-7-21  16 : 10',
          status: '待确定',
          userName: 'kenshin',
          fuwu: '产后通乳'
        }, {
          time: '服务时间：2017-7-21  16 : 10',
          status: '待出诊',
          userName: 'kenshin',
          fuwu: '产后通乳'
        }, {
          time: '服务时间：2017-7-21  16 : 10',
          status: '服务中',
          userName: 'kenshin',
          fuwu: '产后通乳'
        }, {
          time: '服务时间：2017-7-21  16 : 10',
          status: '服务完成',
          userName: 'kenshin',
          fuwu: '产后通乳'
        }],
        startY: -50,
        top: '刷新列表中',
        nav: '全部'
      }
    },
    components: {
      mOFheader
    },
    mounted: function() {
      this.listScroll()
    },
    methods: {
      listScroll: function() {
        var that = this
        Vue.nextTick(() => {
          that.wrapper = new BScroll(that.$refs.wrapper, {
            startY: that.startY,
            click: true
          })
          if (that.wrapper.wrapperHeight < that.wrapper.scrollerHeight) {
            that.add = true
          }
          that.top = '刷新列表中'
          that.wrapper.on('scrollEnd', (pos) => {
            if (pos.y === 0) {
              that.pageNumber = 1
              that.startY = -50
            } else if (pos.y === that.wrapper.maxScrollY) {
              that.startY = that.wrapper.maxScrollY
              that.pageNumber++
            }
          })
        })
      },
      change: function (e) {
        this.nav = e.target.innerText
      }
    }
  }
</script>

<style lang="scss" scoped>
  @import '../assets/css/myOrderForm.scss';
</style>



