<template>
    <div class="patient-message">
        <headers title="消息"></headers>
        <div class="head-operate" v-show="list.length">
            <div class="edit-cancel">
                <div v-show="!edit" class="edit-text" @click="edit=true">编辑</div>
                <div v-show="edit" class="edit-text" :class="{ disabled : checkAll }" @click="checkAllItem">全选</div>
                <div v-show="edit" class="cancel-button" :class="{ disabled : checkAll }" @click="edit=false">取消</div>
            </div>
            <div v-show="edit" class="set-delete" :class="{ active : checkItem.length }">
                <div @click="setRead">设为已读</div>
                <div @click="batchDelete">删除</div>
            </div>
        </div>
        <div class="message-list" v-show="list.length" ref="scroll">
            <ul class="scroll-content">
                <li class="list-item" :class="{ 'is-delete': item.delete , 'check-item' : item.check}" :key="index" v-for="(item, index) in list">
                    <div class="item-left-left" :class="{ check : edit }" @click.stop="toggerDelete(item)">
                        <div v-show="edit" class="point" @click="chooseItem(item, index)">
                            <img v-if="!item.check" src="../../assets/images/syxq/Icon-none@2x.png" alt="">
                            <img v-if="item.check" src="../../assets/images/syxq/Icon-check@2x.png" alt="">
                        </div>
                        <img v-show="!item.read && !edit" class="point" src="./images/red-point.png" alt="red-point">
                        <div class="item-left">
                            <div class="left-head">
                                <div class="left-title">{{ item.type }}</div>
                                <div class="left-time">{{ item.time }}</div>
                            </div>
                            <div class="left-content">{{ item.content }}</div>
                        </div>
                        <div class="item-right">
                            <img :src="item.img" alt="message-1">
                        </div>
                    </div>
                    <div class="item-delete" @click="deleteItem(index)">删除</div>
                </li>
            </ul>
        </div>
        <div class="no-data" v-show="!list.length">
            <img src="./images/no-message.png" alt="no-message">
            <div>您还没有新消息哦~</div>
        </div>
    </div>
</template>
<script>
    import BScroll from 'better-scroll'
    import headers from '../../components/header.vue'
    export default {
        name: "message",
        data () {
            return {
                edit: false,
                checkItem: [],
                checkAll: false,
                list: [
                    {
                        id: "1",
                        img: "src/pages/message/images/message-1.png",
                        type: "订单已被取消",
                        time: "刚刚",
                        read: false,
                        delete: false,
                        check: false,
                        content: "您预定的2017-12-08 11:30产后通乳服务订单已取消，点击查看详情"
                    },{
                        id: "2",
                        img: "src/pages/message/images/message-1.png",
                        type: "收到新的订单",
                        time: "一天前",
                        read: false,
                        delete: false,
                        check: false,
                        content: "您预定的2017-12-08 10:56产后通乳服务已生成订单，点击查看详情"
                    },{
                        id: "3",
                        img: "src/pages/message/images/message-2.png",
                        type: "订单已被取消",
                        time: "2017-12-01",
                        read: true,
                        delete: false,
                        check: false,
                        content: "您预定的2017-12-08 11:30产后催乳的服务服务服务服务订单已取消，点击查看详情"
                    },{
                        id: "4",
                        img: "src/pages/message/images/message-3.png",
                        type: "服务已完成",
                        time: "2017-11-28",
                        read: true,
                        delete: false,
                        check: false,
                        content: "您预定的2017-12-06 10:56产后乳汁淤积服务已完成，点击查看详情"
                    }
                ]
            }
        },
        watch: {
          list : {
              handler (newV) {
                  var _this = this;
                  this.checkItem = [];
                  newV.forEach(function (item) {
                      if (!item.check) {
                          _this.checkAll = false;
                      } else {
                          _this.checkItem.push(item);
                      }
                  })
              },
              deep: true
          }
        },
        components: {
            headers
        },
        methods: {
            /**
             * 点击列表滑出/滑入红色删除按钮
             * auth: ylc
             * date: 2017-12-15 00:58
             * param [item] 列表的一项
             * */
            toggerDelete (item) {
                console.log(111)
                if (this.edit) return;
                item.delete ? item.delete = false : item.delete = true;
            },
            /**
             * 编辑状态下 选中/取消 多选框
             * auth: ylc
             * date: 2017-12-15 00:58
             * param [item] 列表的一项
             * */
            chooseItem (item) {
                item.check ? item.check = false : item.check = true;
            },
            /**
             * 红色删除按钮删除单条数据
             * auth: ylc
             * date: 2017-12-15 00:58
             * param [index] 索引
             * */
            deleteItem (index) {
                this.list.splice(index, 1);
            },
            /**
             * 全选操作
             * auth: ylc
             * date: 2017-12-15 00:58
             * param 无
             * */
            checkAllItem () {
                if (this.checkAll) return;
                this.list.forEach(function (item) {
                    item.check = true;
                })
                this.checkAll = true;
            },
            /**
             * 编辑状态下设为已读
             * auth: ylc
             * date: 2017-12-15 00:58
             * param 无
             * */
            setRead () {
                var _this = this;
                this.list.forEach(function (item) {
                    _this.checkItem.forEach(function (param) {
                        if (item.id == param.id) {
                            item.read = true;
                        }
                    })
                })
            },
            /**
             * 右上角批量删除
             * auth: ylc
             * date: 2017-12-15 00:58
             * param 无
             * */
            batchDelete () {
                var _this = this;
                var idx = [];
                this.list.forEach(function (item, index) {
                    _this.checkItem.forEach(function (param, index2) {
                        if (item.id == param.id) {
                            _this.list.splice(index, 1);
                            _this.checkItem.splice(index2, 1);
                            return;
                        }
                    })
                })
            },
            getListData () {
                var that = this;
                this.$nextTick(function () {
                    if (!that.scroll) {
                        that.scroll = new BScroll(that.$refs.scroll, {})
                        that.scroll.on('touchend', (pos) => {
                            if (pos.y > 50) {
                                that.loadData()
                            }
                        })
                    } else {
                        that.scroll.refresh()
                    }
                });

            },
            loadData () {
                //TODO 请求后台数据
            }

        },
        mounted () {
            this.getListData();
        }
    }
</script>
<style lang="scss" scoped>
    @import "../../assets/css/message.scss";
</style>


